import {Cell, Group, Header} from "@vkontakte/vkui";

const ErrorBlock = (p) => {
    if(p.e.length === 0){
        return ''
    }
    if(true){
        return ''
    }
    const captions = {
        406: ' Alpaca WS.        .      .     .',
        404: ' Alpaca:   '
    }
    return <Group description={'    .   -  ,     '} header={<Header mode={'secondary'}></Header>}>
        {p.e.map(v =>
            <Cell multiline key={v}>
                {captions[v]}
            </Cell>
        )}
    </Group>
}

export default ErrorBlock